import React from 'react';
import "./Card.css";
import { BiPlay, BiPlus } from "react-icons/bi";

const Cards = ({tv}) => {
   
    const base_url = "https://image.tmdb.org/t/p/w500/"; // for posters

  return (
    <>

    <div className="card-movie d-flex flex-grow-0 flex-nowrap flex-shrink-1 m-2 text-start">
    <img
    key={tv.id}
    src={`${base_url}${tv?tv.poster_path:""}`}
    alt={tv.name}
    />
    <div className="description">
      <h3 className="fw-bold">{tv?tv.name: ""}</h3>
      <p>{(tv?tv.overview.slice(0,70)+"..." : "")}</p>
      <div className="d-inline-flex text-center">
        <button 
          type="button" 
          className="d-flex justify-content-center align-items-center btn-play">
          <BiPlay/>Watch
        </button>
        <button 
          type="button" 
          className="d-flex justify-content-center align-items-center ms-2 btn-mylist">
          <BiPlus />My List
        </button>
     </div>
    </div>
   </div>

  </>
  )
}

export default Cards
