import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import Cards from "../Card/Card"

const MovieList = () => {
    const [MovieList, setMovieList] = useState([])
    const {type} = useParams()

    useEffect(() =>{
        getData()
    }, [])

    useEffect(() =>{
        getData()
    }, [type])

    const getData = () => {
        fetch(`https://api.themoviedb.org/3/movie/${type ? type : "popular"}?api_key=ae0a6ae2333419fbec8b274a9a6bca67`)
        .then(res => res.json())
        .then(data => setMovieList(data.results))
    }

  return (
    <div className="container py-5 p-0">
      <h2 className="heading-title">Popular Movies</h2>
      <div className="d-flex justify-content-center flex-wrap py-3 text-center">
        {
            MovieList.map(movie => (
                <Cards movie={movie} />
            ))
        }
      </div>
    </div>
  )
}

export default MovieList
