import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom';
import Cards from "../Card/CardSeries";

const SerieList = () => {
    const [SerieList, setSerieList] = useState([])
    const {type} = useParams()

    useEffect(() =>{
        getData()
    }, [])

    useEffect(() =>{
        getData()
    }, [type])

    const getData = () => {
        fetch(`https://api.themoviedb.org/3/tv/${type ? type : "popular"}?api_key=ae0a6ae2333419fbec8b274a9a6bca67`)
        .then(res => res.json())
        .then(data => setSerieList(data.results))
    }

  return (
    <div className="container py-5 p-0">
      <h2 className="heading-title">Popular Series</h2>
      <div className="d-flex justify-content-center flex-wrap py-3 text-center">
        {
            SerieList.map(tv => (
                <Cards tv={tv} />
            ))
        }
      </div>
    </div>
  )
}

export default SerieList
