import React, {useEffect, useState} from "react"
import "./Movie.css"
import { useParams } from "react-router-dom"
import dateFormat from "dateformat";

const Movie = () => {
    const [currentMovieDetail, setMovie] = useState()
    const { id } = useParams()

    useEffect(() => {
        getData()
    }, [])

    const getData = () => {
        fetch(`https://api.themoviedb.org/3/movie/${id}?api_key=ae0a6ae2333419fbec8b274a9a6bca67&language=en-US`)
        .then(res => res.json())
        .then(data => setMovie(data))
        console.log(currentMovieDetail);
    }

    return (
        <div 
        className="series text-white text-start py-5"
        style={{
        backgroundImage: `linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0) 100%), 
        url(https://image.tmdb.org/t/p/original${currentMovieDetail ? currentMovieDetail.backdrop_path : ""})`,}}
        >
        <div className="container py-5">
          <div className="d-flex flex-column mt-5 ">
            <h1 className="fw-bold">{currentMovieDetail ? currentMovieDetail.title : ""}</h1>

            <div className="flex-shrink-0">
            <span className="tv-rating ">
            {`${Math.floor(currentMovieDetail?currentMovieDetail.vote_average:"" * 10)}%`}
            </span>
            <span className="release-date mx-2">
            
            </span>
            <span className="release-date mx-2">
            {dateFormat(currentMovieDetail?currentMovieDetail.release_date:"", "yyyy - mmm")}
            </span>
            </div>
           
            <div className="d-inline-flex py-3 p-0">
                <button 
                type="button" 
                className="justify-content-center align-items-center btn-play">
                Watch</button>
        
             <button 
              type="button" 
              className="d-flex justify-content-center align-items-center ms-2 btn-mylist">
              My List</button>
            </div>

            <div className="d-flex flex-shrink-0 description text-justify py-3 w-50">
            {(currentMovieDetail?currentMovieDetail.overview.slice(0,250)+"..." : "We don't have an overview translated in English. Help us expand our database by adding one.")}
            </div>


            </div>
          </div>
        </div>
    )
}

export default Movie