import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';

import './App.css';
import { BrowserRouter as Router, Routes, Route } from "react-router-dom"
import Header from './components/Header/Header';
import Home from './pages/Home/Home';
import TVshows from './pages/TV/TV';
import MovieList from './components/List/MovieList';
import Movie from './pages/MovieDetail/Movie';

function App() {
  return (
    <div className="App">
      <Router>
        <Header />
        <Routes>
          <Route index path='/' element={<Home />}></Route>
          <Route exactly path="tv/:type" element={<TVshows />}></Route>
          <Route exactly path="movie/:id" element={<Movie />}></Route>
          <Route exactly path="movies/:type" element={<MovieList />}></Route>
          <Route exactly path='/*' element={<h1>Error Page</h1>}></Route>
        </Routes>
      </Router>
    </div>
  );
}

export default App;
