import React, { useEffect, useState } from 'react';
import { Carousel } from 'react-responsive-carousel';
import './Home.css';
import { Link } from 'react-router-dom';
import MovieList from '../../components/List/MovieList';

import dateFormat from "dateformat";


const Home = () => {

    const [ popularMovies, setPopularMovies ] = useState([])

    useEffect(() => {
      fetch("https://api.themoviedb.org/3/movie/popular?api_key=ae0a6ae2333419fbec8b274a9a6bca67&language=en-US")
      .then(res => res.json())
      .then(data => setPopularMovies(data.results))
  }, [])

  

    return (
      <>
            <Carousel
                showArrows={false}
                showThumbs={false}
                showStatus={false}
                autoPlay={true}
                transitionTime={1000}
                interval={5000}
                infiniteLoop={true}
                showIndicators={true}
                stopOnHover={false}
                autoFocus={true}
            >
                {
                    popularMovies.map(movie => (
                        <div 
                        className="series text-white text-start py-5"
                        key={movie.id}
                        style={{
                        backgroundImage: `linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0) 100%), 
                        url(https://image.tmdb.org/t/p/original/${movie && movie.backdrop_path})`,}}
                        >
                        <div className="container py-5">
                          <div className="d-flex flex-column mt-5 ">
                            <h1 className="fw-bold">{movie ? movie.title: ""}</h1>

                            <div className="flex-shrink-0">
                            <span className="tv-rating ">{`${Math.floor(movie.vote_average * 10)}%`}</span>
                            <span className="release-date mx-2">
                            {dateFormat(movie.release_date, "yyyy - mmm")}
                            </span>
                            <span className="release-date mx-2">

                            </span>
                            </div>
                           
                            <div className="d-inline-flex py-3 p-0">
                              <Link smooth to={`/movie/${movie.id}`}>
                                <button 
                                type="button" 
                                className="justify-content-center align-items-center btn-play">
                                Watch</button>
                              </Link>
                        
                             <button 
                              type="button" 
                              className="d-flex justify-content-center align-items-center ms-3 btn-mylist">
                              My List</button>
                            </div>

                            <div className="d-flex flex-shrink-0 description text-justify py-3 w-50">
                            {(movie?movie.overview.slice(0,250)+"..." : "We don't have an overview translated in English. Help us expand our database by adding one.")}
                            </div>


                            </div>
                          </div>
                        </div>
                    ))
                }
            </Carousel>

        <MovieList/>
  
      </>
    )
}

export default Home
