import React from 'react';
import "./Header.css";
import {NavLink, Link } from "react-router-dom";
import Logo from "./Logo";

const Header = () => {
  return (
    <nav className="navbar navbar-dark navbar-expand-lg p-0 m-3 fixed-top">
    <div className="container">
      <Link to='/' className="navbar-brand">
        <Logo style={{"width" : "40%"}} className="d-inline-block"/>
    </Link>
      <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
      </button>
      <div className="collapse navbar-collapse" id="navbarNav">
        <ul className="navbar-nav ms-auto">
          <li className="nav-item">
          <NavLink 
          end smooth to="/"
          className={({ isActive }) => (isActive ? 'nav-link active' : 'nav-link')}
          >Trending</NavLink>
          </li>
          <li className="nav-item">
          <NavLink 
          end smooth to="/tv/popular" 
          className={({ isActive }) => (isActive ? 'nav-link active' : 'nav-link')}>
          Series</NavLink>
          </li>
          <li className="nav-item">
          <NavLink 
          end smooth to="/movies/popular" 
          className={({ isActive }) => (isActive ? 'nav-link active' : 'nav-link')}
          >Movies</NavLink>
          </li>
          <li className="nav-item dropdown">
            <a className="nav-link dropdown-toggle" role="button" data-bs-toggle="dropdown" aria-expanded="false">
              Genre
            </a>
            <ul className="dropdown-menu">
              <li><a className="dropdown-item">Action</a></li>
              <li><a className="dropdown-item">Adventure</a></li>
              <li><a className="dropdown-item">Animation</a></li>
              <li><a className="dropdown-item">Biography</a></li>
              <li><a className="dropdown-item">Comedy</a></li>
              <li><a className="dropdown-item">Crime</a></li>
              <li><a className="dropdown-item">Documentary</a></li>
              <li><a className="dropdown-item">Drama</a></li>
              <li><a className="dropdown-item" >Family</a></li>
              <li><a className="dropdown-item" >Fantasy</a></li>
              <li><a className="dropdown-item" >Horror</a></li>
              <li><a className="dropdown-item" >Music</a></li>
              <li><a className="dropdown-item" >Mystery</a></li>
              <li><a className="dropdown-item" >Romance</a></li>
              <li><a className="dropdown-item" >Sci-Fi</a></li>
              <li><a className="dropdown-item" >Sport</a></li>
              <li><a className="dropdown-item" >Thriller</a></li>
              <li><a className="dropdown-item" >War</a></li>
              <li><a className="dropdown-item" >Western</a></li>
            </ul>
          </li>
        </ul>
      </div>
    </div>
  </nav>
  )
}

export default Header
