import React, { useEffect, useState } from 'react';

/* customized carousel */
import { Carousel } from 'react-responsive-carousel';
import "./carousel.css"; 

import SeriesList from '../../components/List/SeriesList';

import "./TV.css";
import dateFormat from "dateformat";

const Series = () => {

    const [ popularSeries, setPopularSeries ] = useState([])

    useEffect(() =>{
      getSerieData()
    }, [])

    const getSerieData = () => {
      fetch("https://api.themoviedb.org/3/tv/popular/?api_key=ae0a6ae2333419fbec8b274a9a6bca67")
      .then(res => res.json())
      .then(data => setPopularSeries(data.results))
    }

 
    return (
  <>
      <Carousel autoplay
          showArrows={false}
          showThumbs={false}
          showStatus={false}
          autoPlay={true}
          transitionTime={1500}
          interval={10000}
          infiniteLoop={true}
          showIndicators={true}
          stopOnHover={false}
          autoFocus={true}
          labels={false}
      >
          {
            popularSeries.map(serie => (
        <div 
        className="series text-white text-start py-5"
        style={{
        backgroundImage: `linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0) 100%), 
        url(https://image.tmdb.org/t/p/original/${serie && serie.backdrop_path})`,}}
        >
          <div className="container py-5">
            <div className="d-flex flex-column mt-5 ">
              <h1>{serie ? serie.name: ""}</h1>

              <div className="flex-shrink-0">
               <span className="tv-rating ">{`${Math.floor(serie.vote_average * 10)}%`}</span>
                <span className="release-date mx-3">
                {dateFormat(serie.first_air_date, "yyyy")}
                </span>
                <span className="release-date mx-0">
                   6 Seasons
                </span>
              </div>
              
              <div className="d-inline-flex py-3 p-0">
                <button 
                type="button" 
                className="justify-content-center align-items-center btn-play">
                Watch now</button>

                <button 
                type="button" 
                className="d-flex justify-content-center align-items-center ms-2 btn-mylist">
                My List</button>
              </div>

              <div className="overview d-flex flex-shrink-0 description text-justify w-50">
              {(serie?serie.overview.slice(0,250)+"..." : "We don't have an overview translated in English. Help us expand our database by adding one.")}
              </div>

            </div>
          </div>
        </div>
            ))
          }
      </Carousel>

    <SeriesList />

  </>
    )
}

export default Series
